<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 sm6 class="text-xs-center">
          <h1>||Bhagvad Gita||</h1><br/>
          <h2>Just select the language and start reading...</h2><br/>
      </v-flex>
      <v-flex xs12 class="text-xs-center" v-for="lang in languages" :key="lang.code" v-if="lang.code!='sa'">
        <v-btn block large round color="primary" @click="goto(lang.code)">{{lang.name}}</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import {HTTP} from '@/common/http-common'
export default {
  data () {
    return {
      languages: [],
      errors: []
    }
  },

  mounted () {
    HTTP.get('languages').then(response => {
      this.languages = response.data
    }).catch(e => {
      this.errors.push(e)
    })
  },

  methods: {
    goto (lang) {
      this.$store.dispatch('loadChanges', {lang: lang})
      var verseId = this.$store.getters.getCurrentVerseId
      this.$router.push({
        path: `Verses`,
        query: { scriptureId: this.$store.getters.getScriptureId, previousVerseId: verseId, lang: lang }
      })
    }
  }
}
</script>




