import Vue from 'vue'
import Router from 'vue-router'
import Language from '@/components/Language'
import ReadingCanvas from '@/components/ReadingCanvas'
import Chapters from '@/components/Chapters'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Language
    },
    {
      path: '/Languages',
      name: 'Languages',
      component: Language
    },
    {
      path: '/Verses',
      name: 'ReadingCanvas',
      component: ReadingCanvas,
      props: true
    },
    {
      path: '/Chapters',
      name: 'Chapters',
      component: Chapters
    }
  ]
})



