import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'
import { store } from './store'
import App from './App'
import router from './router'
// import VueIdb from 'vue-idb'

Vue.use(Vuetify)
// Vue.use(VueIdb)

Vue.config.productionTip = false

/* const idb = new VueIdb({
  version: 1,
  database: 'easyvedaz',
  schemas: [
    { allVerses: 'id, number, content, previousVerseId, chapterId, verseType' }
  ]
}) */

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  // idb,
  render: h => h(App),

  created () {
    var lang = this.$store.getters.getLang
    if (lang) {
      var verseId = this.$store.getters.getCurrentVerseId
      router.push('Verses?scriptureId=1&previousVerseId=' + verseId + '&lang=' + lang)
    }
  }
})



