<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 sm6 class="text-xs-center">
          <h1>||Bhagvad Gita||</h1><br/>
          <h2>Select Adhyay / Chapter</h2><br/>
      </v-flex>
      <v-flex xs12 class="text-xs-center" v-for="chapter in chapters" :key="chapter.id" v-if="isLoaded==true">
        <v-btn block large round color="primary" @click="goto(chapter.id)">{{chapter.name}}</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import {HTTP} from '@/common/http-common'
export default {
  data () {
    return {
      isLoaded: false,
      chapters: [],
      errors: []
    }
  },

  computed: {
    scriptureId () {
      return this.$route.query.scriptureId
    },
    lang () {
      return this.$route.query.lang
    }
  },

  mounted () {
    HTTP.get('Chapters', { params: { scriptureId: this.scriptureId, lang: this.lang } }).then(response => {
      this.chapters = response.data
      this.isLoaded = true
    }).catch(e => {
      this.errors.push(e)
    })
  },

  methods: {
    goto (chapterId) {
      this.$store.dispatch('loadChanges', {lang: this.lang, chapterId: chapterId})
      var verseId = this.$store.getters.getCurrentVerseId
      this.$router.push({
        path: `Verses`,
        query: { scriptureId: this.$store.getters.getScriptureId, previousVerseId: verseId, lang: this.lang }
      })
    }
  }
}
</script>




