import Vue from 'vue'
import Vuex from 'vuex'

import {HTTP} from '@/common/http-common'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    verse: null,
    lang: null,
    isLoading: false,
    error: '',
    scriptureId: 1,
    allVerses: null
  },

  getters: {
    getVerse (state) {
      if (state.verse == null) {
        state.verse = JSON.parse(localStorage.getItem('verse'))
      }
      return state.verse
    },
    getLang (state) {
      state.lang = localStorage.getItem('lang')
      return state.lang
    },
    getError (state) {
      return state.errors
    },
    getIsLoaded (state) {
      return state.isLoading
    },
    getScriptureId (state) {
      return state.scriptureId
    },
    getCurrentVerseId (state, getters) {
      var currentVerseId = getters.getVerse != null ? getters.getVerse.id : null
      return currentVerseId
    },
    getAllVerses (state) {
      if (state.allVerses == null) {
        state.allVerses = JSON.parse(localStorage.getItem('allVerses'))
      }
      return state.allVerses
    }
  },

  mutations: {
    setVerse (state, payload) {
      state.verse = payload
      localStorage.setItem('verse', JSON.stringify(payload))
    },
    setLang (state, payload) {
      state.lang = payload
      localStorage.setItem('lang', payload)
    },
    setError (state, payload) {
      state.error.push(payload)
    },
    setIsLoading (state, payload) {
      state.isLoading = payload
    },
    setAllVerses (state, payload) {
      state.allVerses = payload
      localStorage.setItem('allVerses', JSON.stringify(payload))
    }
  },

  actions: {
    loadVerses ({commit, getters}, payload) {
      var isLastVerse = getters.getVerse && getters.getVerse.verseType === 2

      if (getters.getAllVerses === null || isLastVerse) {
        commit('setIsLoading', true)
        var chapterId = 1
        if (payload.chapterId) {
          chapterId = payload.chapterId
        }
        if (isLastVerse) {
          chapterId = getters.getVerse.chapterId + 1
        }
        HTTP.get('Verses', { params: { scriptureId: payload.scriptureId, chapterId: chapterId, lang: payload.lang } }).then(response => {
          commit('setAllVerses', response.data)
          commit('setVerse', response.data[0])
          commit('setIsLoading', false)
        }).catch(e => {
          commit('setError', e)
        })
      } else {
        var currentVerse = getters.getAllVerses.filter(v => v.previousVerseId === getters.getCurrentVerseId)[0]
        commit('setVerse', currentVerse)
      }
    },

    loadChanges ({commit, getters}, payload) {
      commit('setLang', payload.lang)
      var scriptureId = getters.getScriptureId
      var chapterId = getters.getVerse === null ? 1 : getters.getVerse.chapterId
      if (payload.chapterId) {
        chapterId = payload.chapterId
      }

      HTTP.get('Verses', { params: { scriptureId: scriptureId, chapterId: chapterId, lang: payload.lang } }).then(response => {
        commit('setAllVerses', response.data)
        var currentVerse = response.data[0]
        commit('setVerse', currentVerse)
        commit('setIsLoading', false)
      }).catch(e => {
        commit('setError', e)
      })
    },

    back ({commit, getters}, payload) {
      if (getters.getVerse.number !== 1) {
        var currentVerse = getters.getAllVerses.filter(v => v.id === getters.getVerse.previousVerseId)[0]
        commit('setVerse', currentVerse)
      }
    }
  }
})



