<template>
  <v-container>
    <v-layout row wrap v-if="this.$store.getters.getVerse">
      
      <v-flex xs12 sm4 offset-sm4>
        <v-btn color="primary" block @click="goto()">
          <v-icon>arrow_forward</v-icon>
          Next
        </v-btn>
      </v-flex>

      <v-flex xs12 sm6 offset-sm3 class="text-xs-center">

        <v-card>
          <v-card-title>
            <div>
              <h3 v-html=selectedLangText></h3>
              <br><br>
              <span v-html=sanskritText></span>
            </div>
          </v-card-title>
        </v-card>
        
      </v-flex>
      
      <v-flex xs12 sm4 offset-sm4>
        <br><br>
        <v-btn color="gray" flat @click="goback"><v-icon>arrow_back</v-icon>Back</v-btn>
        <v-btn color="gray" flat @click="gotoChapters(1)"><v-icon>list</v-icon>Change Adhyay / Chapter</v-btn>
        <v-btn color="gray" flat to="/Languages"><v-icon>build</v-icon>Change Language</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  computed: {
    scriptureId () {
      return this.$route.query.scriptureId
    },
    currentVerseId () {
      return this.$store.getters.getCurrentVerseId
    },
    chapterId () {
      return this.$route.query.chapterId
    },
    verseNumber () {
      return this.$route.query.verseNumber
    },
    previousVerseId () {
      return this.$route.query.previousVerseId
    },
    lang () {
      return this.$route.query.lang
    },
    selectedLangText () {
      return this.$store.getters.getVerse.content[this.lang].title.replace(/\n/g, '<br />')
    },
    sanskritText () {
      var sanText = this.$store.getters.getVerse.content.sa.title.replace(/\n/g, '<br />')
      return sanText
    }
  },

  created () {
    this.$store.dispatch('loadVerses', { scriptureId: this.scriptureId, previousVerseId: this.previousVerseId, lang: this.lang })
  },

  watch: {
    '$route.query.previousVerseId' (newId, odlId) {
      this.$store.dispatch('loadVerses', { scriptureId: this.scriptureId, previousVerseId: newId, lang: this.lang })
    }
  },

  methods: {
    goto () {
      var verseId = this.$store.getters.getCurrentVerseId
      this.$router.push('Verses?scriptureId=1&previousVerseId=' + verseId + '&lang=' + this.lang)
    },

    goback () {
      this.$store.dispatch('back')
    },

    gotoChapters (scriptureId) {
      this.$router.push({
        path: `Chapters`,
        query: { scriptureId: this.scriptureId, lang: this.lang }
      })
    }
  }
}
</script>


